import { Component, OnInit, Input } from '@angular/core';
import { Observable, Subscription } from 'rxjs';
import { map } from 'rxjs/operators';
import { Paginated } from '@feathersjs/feathers';
import { DataService } from '../../core/services/data.service';

@Component({
  selector: 'app-shared-record-user-groups',
  templateUrl: './shared-record-user-groups.component.html',
  styleUrls: ['./shared-record-user-groups.component.scss']
})
export class SharedRecordUserGroupsComponent implements OnInit {

  @Input('record') record: any;

  groups: any = [];

  loading: boolean = true;

  constructor(
    private dataService: DataService
  ) { }

  ngOnInit(): void {

    this.dataService.findRecords('group-members', {
      user: this.record._id
    })
    .then( async res => {
      const groups = res.data;

      for ( var g in groups ) {
        let grp = await this.dataService.getRecord('groups', groups[g].group);

        this.groups.push(grp);

      }

    })
    .then( () => {
      this.loading = false;
    })
    .catch( err => {
      console.log(err);
    })
  }

}
